const privacyPolicyJson = {
	"content": "<div class=\"ce_text privacy_item general_part1 block\"><h1>Datenschutzerklrung</h1><div class=\"text\"><p>Mit dieser Datenschutzerklrung mchten wir Sie ber Art, Umfang und Zweck der Verarbeitung von personenbezogenen Daten (im Folgenden auch nur als \"Daten\" bezeichnet) aufklren. Personenbezogene Daten sind alle Daten, die einen persnlichen Bezug zu Ihnen aufweisen, z. B. Name, Adresse, E-Mail-Adresse oder Ihr Nutzerverhalten. Die Datenschutzerklrung gilt fr alle von uns vorgenommene Daten-Verarbeitungsvorgnge sowohl im Rahmen unserer Kernttigkeit als auch fr die von uns vorgehaltenen Online-Medien.</p></div></div><div class=\"ce_text privacy_item contact_responsible block\"><div class=\"text\"><h2>Wer bei uns fr die Datenverarbeitung verantwortlich ist</h2><p>Verantwortlich fr die Datenverarbeitung ist:</p></div></div><p>Universitt Osnabrpck<br>Neuer Graben 29 / Schloss<br>49074 Osnabrck<br>Deutschland<br>+49 541 969 0<br></p><div class=\"ce_text privacy_item contact_dataprotectioncommissioner block\"><div class=\"text\"><h2>Kontaktdaten unseres Datenschutzbeauftragten</h2><p>Unseren Datenschutzbeauftragten knnen Sie per E-Mail unter datenschutzbeauftragter@uni-osnabrueck.de oder unter unserer Postadresse mit dem Zusatz an den Datenschutzbeauftragten erreichen.</p></div></div><div class=\"ce_text privacy_item general_part3 block\"><div class=\"text\"><h2>Ihre Rechte nach der DSGVO</h2><p>Nach der DSGVO stehen Ihnen die nachfolgend aufgefhrten Rechte zu, die Sie jederzeit bei dem in Ziffer 1. dieser Datenschutzerklrung genannten Verantwortlichen geltend machen knnen:</p><ul><li><strong>Recht auf Auskunft:</strong> Sie haben das Recht, von uns Auskunft darber zu verlangen, ob und welche Daten wir von Ihnen verarbeiten.</li><li><strong>Recht auf Berichtigung:</strong> Sie haben das Recht, die Berichtigung unrichtiger oder Vervollstndigung unvollstndiger Daten zu verlangen.</li><li><strong>Recht auf Lschung:</strong> Sie haben das Recht, die Lschung Ihrer Daten zu verlangen.</li><li><strong>Recht auf Einschrnkung:</strong> Sie haben in bestimmten Fllen das Recht zu verlangen, dass wir Ihre Daten nur noch eingeschrnkt bearbeiten.</li><li><strong>Recht auf Datenbertragbarkeit:</strong> Sie haben das Recht zu verlangen, dass wir Ihnen oder einem anderen Verantwortlichen Ihre Daten in einem strukturierten, gngigen und maschinenlesebaren Format bermitteln.</li><li><strong>Beschwerderecht</strong>: Sie haben das Recht, sich bei einer Aufsichtsbehrde zu beschweren. Zustndig ist die Aufsichtsbehrde Ihres blichen Aufenthaltsortes, Ihres Arbeitsplatzes oder unseres Firmensitzes.</li></ul><h3>Widerrufsrecht</h3><p>Sie haben das Recht, die von Ihnen erteilte Einwilligung zur Datenverarbeitung jederzeit zu widerrufen.</p><h3>Widerspruchsrecht</h3><p>Sie haben das Recht, jederzeit gegen die Verarbeitung Ihrer Daten, die wir auf unser berechtigtes Interesse nach Art. 6 Abs. 1 lit. f DSGVO sttzen, Widerspruch einzulegen. Sofern Sie von Ihrem Widerspruchsrecht Gebrauch machen, bitten wir Sie um die Darlegung der Grnde. Wir werden Ihre personenbezogenen Daten dann nicht mehr verarbeiten, es sei denn, wir knnen Ihnen gegenber nachweisen, dass zwingende schutzwrdige Grnde an der Datenverarbeitung Ihre Interessen und Rechte berwiegen.</p><p><span style=\"text-decoration: underline;\"><strong>Unabhngig vom vorstehend Gesagten, haben Sie das jederzeitige Recht, der Verarbeitung Ihrer personenbezogenen Daten fr Zwecke der Werbung und Datenanalyse zu widersprechen.</strong></span></p><p>Ihren Widerspruch richten Sie bitte an die oben angegebene Kontaktadresse des Verantwortlichen.</p><h2>Wann lschen wir Ihre Daten?</h2><p>Wir lschen Ihre Daten dann, wenn wir diese nicht mehr brauchen oder Sie uns dies vorgeben. Dass bedeutet, dass - sofern sich aus den einzelnen Datenschutzhinweisen dieser Datenschutzerklrung nichts anderes ergibt - wir Ihre Daten lschen,</p><ul><li>wenn der Zweck der Datenverarbeitung weggefallen ist und damit die jeweilige in den einzelnen Datenschutzhinweisen genannte Rechtsgrundlage nicht mehr besteht, also bspw.<ul><li>nach Beendigung der zwischen uns bestehenden vertraglichen oder mitgliedschaftlichen Beziehungen (Art. 6 Abs. 1 lit. a DSGVO) oder</li><li>nach Wegfall unseres berechtigten Interesses an der weiteren Verarbeitung oder Speicherung Ihrer Daten (Art. 6 Abs. 1 lit. f DSGVO),</li></ul></li><li>wenn Sie von Ihrem Widerrufsrecht Gebrauch machen und keine anderweitige gesetzliche Rechtsgrundlage fr die Verarbeitung im Sinne von Art. 6 Abs. 1 lit. b-f DSGVO eingreift,</li><li>wenn Sie vom Ihrem Widerspruchsrecht Gebrauch machen und der Lschung keine zwingenden schutzwrdigen Grnde entgegenstehen.</li></ul><p>Sofern wir (bestimmte Teile) Ihre(r) Daten jedoch noch fr andere Zwecke vorhalten mssen, weil dies etwa steuerliche Aufbewahrungsfristen (in der Regel 6 Jahre fr Geschftskorrespondenz bzw. 10 Jahre fr Buchungsbelege) oder die Geltendmachung, Ausbung oder Verteidigung von Rechtsansprchen aus vertraglichen Beziehungen (bis zu vier Jahren) erforderlich machen oder die Daten zum Schutz der Rechte einer anderen natrlichen oder juristischen Person gebraucht werden, lschen wir (den Teil) Ihre(r) Daten erst nach Ablauf dieser Fristen. Bis zum Ablauf dieser Fristen beschrnken wir die Verarbeitung dieser Daten jedoch auf diese Zwecke (Erfllung der Aufbewahrungspflichten).</p></div></div><div class=\"ce_text privacy_item general_part2 block\"><div class=\"text\"><h2>Cookies</h2><p>Unsere Internetseite nutzt Cookies. Bei Cookies handelt es sich um kleine Textdateien, bestehend aus einer Reihe von Zahlen und Buchstaben, die auf dem von Ihnen genutzten Endgert abgelegt und gespeichert werden. Cookies dienen vorrangig dazu, Informationen zwischen dem von Ihnen genutzten Endgert und unserer Webseite auszutauschen. Hierzu gehren u. a. die Spracheinstellungen auf einer Webseite, der Login-Status oder die Stelle, an der ein Video geschaut wurde.</p><p>Beim Besuch unserer Webseiten werden zwei Typen von Cookies eingesetzt:</p><ul><li><strong>Temporre Cookies (Session Cookies):</strong> Diese speichern eine sogenannte Session-ID, mit welcher sich verschiedene Anfragen Ihres Browsers der gemeinsamen Sitzung zuordnen lassen. Die Session-Cookies werden gelscht, wenn Sie sich ausloggen oder Ihren Browser schlieen.</li><li><strong>Permanente Cookies: </strong>Permanente Cookies bleiben auch nach dem Schlieen des Browsers gespeichert. Dadurch erkennt unsere Webseite Ihren Rechner wieder, wenn Sie auf unsere Webseite zurckkehren. In diesen Cookies werden beispielsweise Informationen zu Spracheinstellungen oder Log-In-Informationen gespeichert. Auerdem kann mit diesen Cookies Ihr Surfverhalten dokumentiert und gespeichert werden. Diese Daten knnen zu Statistik-, Marketing- und Personalisierungs-Zwecken verwendet werden.</li></ul><p>Neben der vorstehenden Einteilung knnen Cookies auch im Hinblick auf ihren Einsatzzweck unterschieden werden:</p><ul><li><strong>Notwendige Cookies:</strong> Dies sind Cookies, die fr den Betrieb unserer Webseite unbedingt erforderlich sind, um Logins oder Warenkrbe fr die Dauer Ihrer Sitzung zu speichern oder Cookies, die aus Sicherheitsgrnden gesetzt werden.</li><li><strong>Statistik-, Marketing- und Personalisierungs-Cookies:</strong> Dies sind Cookies, die fr Analysezwecke oder die Reichweitenmessung eingesetzt werden. ber solche \"Tracking\"-Cookies knnen insbesondere Informationen zu eingegebenen Suchbegriffen oder die Hufigkeit von Seitenaufrufen gespeichert sein. Daneben kann aber auch das Surfverhalten eines einzelnen Nutzers (z. B. Betrachten bestimmter Inhalte, Nutzen von Funktionen etc.) in einem Nutzerprofil gespeichert werden. Solche Profile werden genutzt, um Nutzern Inhalte anzuzeigen, die deren potentiellen Interessen entsprechen. Soweit wir Dienste nutzen, ber die Cookies zu Statistik-, Marketing- und Personalisierungs-Zwecken auf Ihrem Endgert gespeichert werden, informieren wir Sie hierzu gesondert in den nachfolgenden Abschnitten unserer Datenschutzerklrung oder im Rahmen der Einholung Ihrer Einwilligung.</li></ul><p><strong>Betroffene Daten:</strong></p><ul><li>Nutzungsdaten (bspw. Zugriffszeiten, angeklickte Webseiten)</li><li>Kommunikationsdaten (bspw. Informationen ber das genutzte Gert, IP-Adresse).</li></ul><p><strong>Betroffene Personen: </strong>Nutzer unserer Onlineangebote</p><p><strong>Verarbeitungszweck: </strong>Ausspielen unserer Internetseiten, Gewhrleistung des Betriebs unserer Internetseiten, Verbesserung unseres Internetangebotes, Kommunikation und Marketig</p><p><strong>Rechtsgrundlage:<br>Berechtigtes Interesse, Art. 6 Abs. 1 lit. f DSGVO</strong><br>Sofern wir von Ihnen keine Einwilligung in das Setzen der Cookies einholen, sttzen wir die Verarbeitung Ihrer Daten auf unser berechtigtes Interesse, die Qualitt und Benutzerfreundlichkeit unseres Internetauftritts, insbesondere der Inhalte und Funktionen zu verbessern. Sie haben ber die Sicherheitseinstellungen Ihres Browsers, dem Einsatz der von uns im Rahmen unseres berechtigten Interesses gesetzten Cookies zu widersprechen. Dort haben Sie die Mglichkeit festzulegen, ob Sie etwa von vornherein keine oder nur auf Nachfrage Cookies akzeptieren oder aber festlegen, dass Cookies nach jedem Schlieen Ihres Browsers gelscht werden. Werden Cookies fr unsere Webseite deaktiviert, knnen mglicherweise nicht mehr alle Funktionen der Webseite vollumfnglich genutzt werden.</p></div></div><div class=\"ce_text privacy_item registry block\"><div class=\"text\"><h2>Registrierung, Anmeldung und Nutzerkonto</h2><p>Sie haben die Mglichkeit sich auf unserem Online-Medium zu registrieren, um dort ein Nutzerkonto anzulegen. Hierzu ist die Angabe personenbezogener Daten notwendig, die sich aus der Eingabemaske ergeben. Zu den dort abgefragten Daten gehren insbesondere Ihr Name, Ihre E-Mail-Adresse, ggfls. ein Nutzername sowie das Passwort. Diese Daten werden von uns gespeichert und verarbeitet, um fr Sie ein Nutzerkonto einzurichten und die (wiederholte) Anmeldung zu ermglichen. Die Daten knnen jederzeit durch Sie gendert oder gelscht werden. Die Daten werden nicht an Dritte weitergegeben auer dies dient der technischen und organisatorischen Abwicklung des zwischen uns bestehenden Nutzungsvertrages. Um Sie und uns vor missbruchlichen Registrierungen zu schtzen, speichern wir die Ihnen im Zeitpunkt der Registrierung zugeordnete IP-Adresse, sowie das Datum und die Uhrzeit der Registrierung.</p><p><strong>Betroffene Daten:</strong></p><ul><li>Bestandsdaten (bspw. Namen, Adressen)</li><li>Kontakdaten (bspw. E-Mail-Adresse, Telefonnummer, Postanschrift)</li><li>Vertragsdaten (bspw. Vertragsgegenstand, Vertragsdauer)</li><li>Zahlungsdaten (bspw. Bankverbindungsdaten, Rechnungen)</li><li>Inhaltsdaten (bspw. Posts, Fotos, Videos)</li><li>Nutzungsdaten (bspw. Zugriffszeiten, angeklickte Webseiten)</li><li>Kommunikationsdaten (bspw. Informationen ber das genutzte Gert, IP-Adresse)</li></ul><p><strong>Verarbeitungszweck: </strong>Abwicklung vertraglicher Leistungen, Kommunikation sowie Beantwortung von Kontaktanfragen, Sicherheitsmanahmen.</p><p><strong>Rechtsgrundlage:</strong> Vertragserfllung und vorvertragliche Anfragen, Art. 6 Abs. 1 lit. b DSGVO, rechtliche Verpflichtung, Art. 6 Abs. 1 lit. c DSGVO, berechtigtes Interesse, Art. 6 Abs. 1 lit. f DSGVO</p><p><strong>Lschung:</strong> Siehe hierzu den Punkt: <strong>\"Wann lschen wir Ihre Daten?\"</strong>. Daneben mchten wir Sie darauf aufmerksam machen, dass wir die im Rahmen der Registrierung erhobenen Daten sowie die in dem Account hinterlegten Inhaltsdaten vorbehaltlich entgegenstehender gesetzlicher Aufbewahrungspflichten lschen, sobald Sie Ihren Account lschen. Wir bitten Sie daher, sofern Sie auf die in Ihrem Account hinterlegten Inhaltsdaten auch nach der Lschung Ihres Account zugreifen wollen oder mssen, diese vor der Lschung des Accounts anderweitig zu sichern.</p></div></div><div class=\"ce_text privacy_item blog block\"><div class=\"text\"><h2>Ihre Kommentare oder Bewertungen</h2><p>Sie haben die Mglichkeit, auf den dafr vorgesehenen Bereichen unserer Internetprsenz unsere Beitrge zu kommentieren, Ihre Meinung zu uern oder Content einzustellen. Da wir fr rechtsverletzende Inhalte Ihres Kommentars (Beleidigung, Schmhkritik, Volksverhetzung, verbotene Gewaltdarstellung etc.) unter Umstnden selbst in Haftung genommen werden knnen, speichern wir Ihre IP-Adresse fr die Dauer von 7 Tagen, um gegebenenfalls hierber Ihre Identitt ermitteln zu knnen.</p><p><strong>Rechtsgrundlage</strong>: Art. 6 Abs. 1 lit. f DSGVO</p><p><strong>Betroffene Daten:</strong></p><ul><li>Bestandsdaten (bspw. Namen, Adressen)</li><li>Inhaltsdaten (bspw. Posts, Fotos, Videos)</li><li>Nutzungsdaten (bspw. Zugriffszeiten, angeklickte Webseiten)</li><li>Kommunikationsdaten (bspw. Informationen ber das genutzte Gert, IP-Adresse)</li></ul><p><strong>Verarbeitungszweck: </strong>Leistungserfllung vertraglicher Verpflichtungen, Kommunikation und Abwicklung von Anfragen, Einholen von Feedback, Sicherheitsmanahmen.</p><p></p></div></div><div class=\"ce_text block\"><div class=\"text\"><h2>Chat-Dienste</h2><p>Wir kommunizieren ber Online-Chats und/oder Chatbots (nachfolgend als Chat-Dienste bezeichnet). Unter einem Online-Chat versteht man den Austausch von Text-Nachrichten ber das Internet in Echtzeit. Chat-Bots sind Computer-Programme, die in der Regel auf Internetseiten eingesetzt werden, um Nutzerfragen automatisiert zu beantworten oder Benachrichtigungen an Nutzer auszuspielen.</p><p>Sofern Sie unsere Chat-Dienste nutzen, erheben wir Ihre Zugriffs-Daten. Hierzu gehren insbesondere die IP-Adresse des anfragenden Gerts, das Datum und die Uhrzeit der Anfrage sowie Ihre Identifikationsnummer. Ferner speichern wir den Inhalt der ber die Chat-Dienste gefhrte Kommunikation. Schlielich protokollieren wir Ihre Registrierung und Ihre Einwilligungserklrung. Diese Daten nutzen wir, um mit Ihnen ber unsere Chat-Dienste kommunizieren zu knnen, Sie persnlich anzusprechen, um Ihre Anfragen zu beantworten sowie etwaige angeforderte Inhalte zu bermitteln.</p><p>Der oder die unten genannte/n Diensteanbieter hat/haben die Mglichkeit, auf die Metadaten der Kommunikation zuzugreifen. Hierzu gehren Zeit und (je nach Einstellung) Ort der Kommunikation sowie das von Ihnen verwendete Gert sowie die Information wer mit wem kommuniziert hat. Der Zugriff auf die Metadaten durch den Diensteanbieter erfolgt in der Regel um den Dienst zu optimieren oder aus Sicherheitsgrnden. Daneben kann der Zugriff durch den Diensteanbieter aber auch erfolgen, um die Daten zu Marketingzwecken einzusetzen. Nheres hierzu erfahren Sie in der Datenschutzerklrung des Diensteanbieters, die wir nachfolgend verlinkt haben.</p><p>Wir mchten Sie darauf hinweisen, dass je nach Sitzland des unten genannten Anbieters die ber dessen Plattform erfassten Daten auerhalb des Raumes der Europischen Union bertragen und verarbeitet werden knnen. Es besteht in diesem Fall das Risiko, dass das von der DSGVO vorgeschriebene Datenschutzniveau nicht eingehalten und die Durchsetzung Ihrer Rechte nicht oder nur erschwert erfolgen kann.</p><p><strong>Betroffene Daten:</strong></p><ul><li>Bestands- und Kontaktdaten (bspw. Name, Telefonnummer, E-Mail-Adresse)</li><li>Inhaltsdaten (bspw. Twexteingaben)</li><li>Nutzungsdaten (bspw. Zugriffszeiten, Interesse an Inhalten)</li><li>Kommunikationsdaten (bspw. Informationen ber das genutzte Gert, IP-Adresse).</li></ul><p><strong>Verarbeitungszweck: </strong>Kommunikation und Marketing</p><p><strong>Rechtsgrundlage:</strong> Sofern wir Sie vor dem Einsatz des jeweiligen Chat-Dienstes um Ihre Einwilligung gebeten haben, liegt hierin die Rechtsgrundlage, Art. 6 Abs. 1 lit. a DSGVO. Insofern stellen wir klar, dass wir die Ihre Kontaktdaten ohne Ihre Einwilligung nicht erstmalig an den Diensteanbieter bermitteln. Sofern wir mit Ihnen im Rahmen einer Vertragsanbahnung oder im Rahmen eines bestehenden vertraglichen Verhltnisses ber einen der nachfolgenden genannten Dienste kommunizieren, besteht die Rechtsgrundlage in der Erfllung oder Vorbereitung des Vertrages, Art. 6 Abs. 1 lit. b DSGVO. Im brigen berufen wir uns auf unsere berechtigten Interessen an einer schnellen und effizienten Kommunikation und Erfllung der Bedrfnisse unser Kommunikationspartner an der Kommunikation der nachfolgend genannten Dienste, Art. 6 Abs. 1 lit. f DSGVO.</p><p><strong>Widerspruchsmglichkeiten: </strong>Die uns zur Nutzung des nachfolgenden genannten Dienstes erteilte Einwilligung knnen Sie jederzeit widerrufen. Der Verarbeitung Ihrer Daten ber die von uns genutzten Chat-Dienst knnen Sie zudem jederzeit widersprechen.</p><p><strong>Wir nutzen folgende Chatbots und Chatfunktionen:</strong></p></div></div><div class=\"ce_text privacy_item general_part4 block\"><div class=\"text\"><h2>Sicherheitsmanahmen</h2><p>Wir treffen im brigen technische und organisatorische Sicherheitsmanahmen nach dem Stand der Technik, um die Vorschriften der Datenschutzgesetze einzuhalten und Ihre Daten gegen zufllige oder vorstzliche Manipulationen, teilweisen oder vollstndigen Verlust, Zerstrung oder gegen den unbefugten Zugriff Dritter zu schtzen.</p><h2>Aktualitt und nderung dieser Datenschutzerklrung</h2><p>Diese Datenschutzerklrung ist aktuell gltig und hat den Stand Dezember 2021. Aufgrund genderter gesetzlicher bzw. behrdlicher Vorgaben kann es notwendig werden, diese Datenschutzerklrung anzupassen.</p><p><strong>Diese Datenschutzerklrung wurde mit Hilfe des Datenschutz-Generators von SOS Recht erstellt. SOS Recht ist ein Angebot der Mueller.legal Rechtsanwlte Partnerschaft mit Sitz in Berlin.</strong></p></div></div>"
}
